	module.exports = {"pathContext":{"docHtml":"<h1>Argo CD ssh<em>known</em>hosts file customization</h1>\n<p>The directory contains sample kustomize application which customizes <code>/etc/ssh/ssh_known_hosts</code> file in Argo CD. This is useful if you want to disable SSL cert validation\nfor Git repositories connected using SSL urls:</p>\n<ul>\n<li><code>argocd-known-hosts-mounts.yaml</code> - define merge patches which inject <code>/etc/ssh/ssh_known_hosts</code> file mount into all Argo CD deployments.</li>\n<li><code>argocd-known-hosts.yaml</code> - defines <code>ConfigMap</code> which includes <code>/etc/ssh/ssh_known_hosts</code> file content.</li>\n<li><code>kustomization.yaml</code> - Kustomize application which bundles stable version of Argo CD and apply <code>argocd-known-hosts-mounts.yaml</code> patches on top.</li>\n</ul>\n<p>!!! note\nThe <code>/etc/ssh/ssh_known_hosts</code> should include Git host on each Argo CD deployment as well as on a computer where <code>argocd repo add</code> is executed. After resolving issue\n<a href=\"https://github.com/argoproj/argo-cd/issues/1514\">#1514</a> only <code>argocd-repo-server</code> deployment has to be customized.</p>","docPath":"argo-cd/examples/known-hosts/readme","proj":"argo-cd"}}

